<template>
  <div id="app" class="wrapper">
    <header class="header">
      <ul class="nav">
        <li @click="showTabNav('/en')" v-show="isActive">View in English</li>
        <li @click="showTabNav('/cn')" v-show="!isActive">中文版</li>
      </ul>
    </header>
    <section class="main">
      <router-view />
    </section>
    <footer class="footer">
      <div class="foot">
        <p>©2019 JiaHua</p>
      </div>
    </footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isActive: false
    };
  },
  created() {
    if (location.hash.replace(/#/, "") === "/cn") {
      this.isActive = true;
    } else {
      this.isActive = false;
    }
  },
  methods: {
    showTabNav(e) {
      console.log(e);
      this.isActive = !this.isActive;
      this.$router.push(e);
    }
  }
};
</script>

